import React, { useState, useEffect } from 'react';

const Admin = () => {

    const [key, setKey] = useState('');
    const [action, setAction] = useState('view');
    const [data, setData] = useState(null);
    const [message, setMessage] = useState(null);
    const [editableData, setEditableData] = useState('');

    const defaultData = [
        {
            "skill": "Surfing",
            "time": "00:00:00",
            "percentage": "0"
        }
    ];

    useEffect(() => {
        console.log(`editableData: ${editableData}`);
    }, [editableData]);

    useEffect(() => {
        console.log(`data: ${data}`);
    }, [data]);

    const handleView = () => {
        const storedData = localStorage.getItem(key);
        if (storedData) {
            const displayData = JSON.stringify(JSON.parse(storedData), null, 2);
            //const formattedData = storedData
            const formattedData = displayData
                .replace(/,/g, ',\n')
                .replace(/}/g, '\n}')
                .replace(/{/g, '\n{\n')
                .replace(/]/g, ']\n')
                .replace(/\[/g, '[\n');
            //setData(formattedData);
            setMessage(null);
            setData(displayData);
        } else {
            setMessage(`No data found for ${key}.`);
        }
    };
    const handleNew = () => {
        const storedData = localStorage.getItem(key);
        if (storedData) {
            setMessage(`Data already exists for ${key}.`);
        } else {
            const displayData = JSON.stringify(defaultData, null, 2);
            setMessage(null);
            setData(displayData);
        }
    };

    const handleRemove = () => {
        localStorage.removeItem(key);
        setMessage(`Data for key '${key}' has been removed.`);
        setData(null);
        setEditableData('');
    };

    const handleEdit = () => {
        localStorage.setItem(key, data);
        setData(null);
        setEditableData('');
        setMessage(`Changes have been saved to ${key}`);
    };
    const handleAdd = () => {
        localStorage.setItem(key, data);
        setData(null);
        setEditableData('');
        setMessage(`${key} has been added to localStorage`);
    };
    const handleActionChange = (e) => {
        setAction(e.target.value);
        setData(null);
        setMessage(null);
        setEditableData('');
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (key.trim() === '') {
            alert('Please enter a localStorage key.');
            return;
        }
        if (action === 'view') {
            handleView();
        } else if (action === 'remove') {
            handleRemove();
        } else if (action === 'edit') {
            handleView(); // Load data for editing
        } else if (action === 'add') {
            handleNew();
        }
    };
    const getData = (data) => {
        const text = JSON.stringify(data, null, 2);
        const formattedText = text.replace(/\\/g, '\n');
        return formattedText;
    }

    return (
        <div className='containerBox'>
            <div className='containerBox bg-lite'>
                <h2>Admin Tool</h2>
            </div>
            <form className='containerBox' onSubmit={handleSubmit}>
                <div>
                    <div className='containerBox p-20 bold'>LocalStorage</div>
                    <label className='flexContainer'>
                        <div className='containerDetail pt-20 pb-20 flex2Column contentRight'>
                            Key:
                        </div>
                        <input
                            className='containerBox flex2Column contentLeft width-50-percent'
                            type="text"
                            value={key}
                            onChange={(e) => setKey(e.target.value)}
                        />
                    </label>
                </div>
                <div className='containerBox'>
                    <label className='flexContainer'>
                        <div className='containerDetail pt-20 pb-20 flex2Column contentRight'>
                            Action:
                        </div>
                        <select
                            className='containerBox flex2Column contentLeft'
                            value={action}
                            onChange={handleActionChange}
                        >
                            <option value="view">View</option>
                            <option value="edit">Edit</option>
                            <option value="add">Add</option>
                            <option value="remove">Remove</option>
                        </select>
                    </label>
                </div>
                <button className='containerBox button width-100-percent bg-green' type="submit">Select</button>
            </form>

            {action === 'view' && data && (
                <div className='containerBox width-100-percent'>
                    <h3>View Data</h3>
                    <pre className='containerBox width-100-percent contentLeft'>
                        {data}
                    </pre>
                </div>
            )}

            {action === 'edit' && data && (
                <div className='containerBox width-100-percent'>
                    <h3>Edit Data</h3>
                    <textarea
                        className='containerBox width-100-percent' 
                        rows="10"
                        cols="50"
                        value={data}
                        onChange={(e) => setData(e.target.value)}
                    />
                    <br />
                    <div className='containerBox button width-100-percent bg-green' onClick={handleEdit}>Save Changes</div>
                </div>
            )}
            {action === 'add' && data && (
                <div className='containerBox width-100-percent' >
                    <h3>Add Data</h3>
                    <textarea
                        className='containerBox width-100-percent' 
                        rows="10"
                        cols="50"
                        value={data}
                        onChange={(e) => setData(e.target.value)}
                    />
                    <br />
                    <div className='containerBox button width-100-percent bg-green'  onClick={handleAdd}>Add Data</div>
                </div>
            )}
            {message && (
                <div className='containerBox width-100-percent color-neogreen'>
                    <h3>{message}</h3>
                </div>
            )}
        </div>
    );
};

export default Admin;